<template>
    <div>
        <div class="box">
            <div class="search-box clearfix" ref="searchBox">
                <div class="search fl">
                    <van-search placeholder="请输入搜索关键词"  />
                </div>
                <div class="relase fr">
                    <span><van-icon name="add-o" size=".8rem"/></span>
                    <span>发布</span>
                </div>
            </div>


            <div class="tabs-list-box" >

                <van-tabs @click="onClick">
                    <van-tab v-for="(item,index) in tabsData.data" :key="index" :title="item.value">

                        <div class="content-box" ref="contentBox" @scroll="load">

                            <div class="content" ref="content" v-for="(item,index) in listData" :key="index" @click="newsDetails(item.item_id,item)">
                                <div class="header clearfix">
                                    <span class="fl">来源:{{item.source}}</span>
                                    <span class="fr">{{item.chinese_tag}}</span>
                                </div>
                                <div class="news-content">
                                    <div class="img fl">
                                        <!-- <img class="auto-img" :src="item.middle_image" alt=""> -->
                                        <img class="auto-img" :src="item.media_info.avatar_url" alt="">
                                    </div>
                                    <div class="text fr">
                                        <div class="title">{{item.title}}</div>

                                        <div class="abstract">{{item.abstract}}</div>
                                    </div>
                                </div>
                            </div>
                            
                        </div>

                    </van-tab>
                </van-tabs>
            </div>
        </div>
        <div>
            <router-view />
        </div>
    </div>
</template>

<script>

    import $ from 'jquery'
    import Axios from 'axios'

    export default {
        name:'smallVideo',

        data(){
            return {
                tabsData:[],

                alllistData:[],

                listData:[],

                key:'',

                allList:[],

                allListData:[],

                a:0,

                count:4



            }
        },


        created(){
            let _this = this

            //加载提示
            this.$toast.loading({
                duration: 0,
                message: "加载中..."
            });

            
             //请求数据
            this.axios({
                //请求类型
                method:'GET',
                //请求地址
                url:'http://api01.6bqb.com/toutiao/category?apikey=9152B061C54746870D248AA78B33EA05'
                
            }).then(result =>{
                // console.log('result111111==>',result)
                _this.tabsData = result.data
                // console.log('_this.tabsData ==>',_this.tabsData)
          
            }).catch(err => {
                // console.log('err ==> ', err);
            })


            // 第一个要显示的数据
            this.axios({
                //请求类型
                method:'GET',
                //请求地址
                url:'http://api01.6bqb.com/toutiao/search?apikey=9152B061C54746870D248AA78B33EA05&tag=__all__'
                
            }).then(result =>{
                // console.log('result2222==>',result)
                _this.alllistData = result.data
                // console.log('_this.alllistData==>',_this.alllistData)
                _this.listData = _this.alllistData.data.slice(0,_this.count)
                // console.log('_this.listData ==>',_this.listData)

                //取消加载提示
                this.$toast.clear();
            }).catch(err => {
                //取消加载提示
                this.$toast.clear();
            })
        },


        methods:{


             // 懒加载
            load(){
                let _this = this

                //保存当前滚动的所有定时器序号
                var timers = [];

                var clearTime = setTimeout(function () {
                    for (var i = 1; i < timers.length; i++) {
                        clearTimeout(timers[i]);
                    }

                    let searchBox = _this.$refs.searchBox.clientHeight
                    // console.log('searchBox ==>',searchBox)

                    // 获取最后一个节点
                    let last = _this.$refs.content[_this.$refs.content.length -1]
                    // console.log('last ==>',last)

                    // 最后一个节点可视窗口的距离
                    let lastHeight = last.getBoundingClientRect();
                    let top = parseFloat(lastHeight.top)
                    // console.log('top ==>',top)

                    // 每个内容的高度
                    let contentHeight = _this.$refs.content[0].clientHeight
                    // console.log('contentHeight ==>',contentHeight)

                    // 大盒子的高度
                    let contentBox = _this.$refs.contentBox[0].offsetHeight
                    // console.log('contentBox ==>',contentBox)

                    let isPass = contentBox - contentHeight + searchBox  + 24 + 44
                    // console.log('isPass ==>',isPass)

                    if(isPass >=top){

                        _this.$toast.loading({
                            duration:0,
                            message: "加载中..."
                        });                    
                        _this.listData = _this.alllistData.data.slice(0,_this.count)
                        _this.count +=4

                        if(_this.listData.length < 4){
                            _this.isLoad = false
                        }
                        //取消加载提示
                        _this.$toast.clear();       
                    }
                    timers = [];      
                })
                timers.push(clearTime);
            },
            
            // 点击切换tabs导航切换不同的内容
            onClick(index){

                this.count = 4

                let _this = this
                this.key = this.tabsData.data[index].key
                console.log(this.key)

                let allListData = localStorage.getItem('allListData')
                // console.log('allListData11111111 ==>',allListData)
                
                allListData =JSON.parse(allListData)
                // console.log('allListData 23423423==>',allListData)

                if(allListData == null){
                    allListData = []
                    this.listData.key = '__all__'
                    // console.log('this.listData4444444444==>',this.listData)
                    allListData.push(this.listData)
                }
                // console.log('allListData ==>',allListData)
                // console.log('this.key ==>',this.key)
                // let a
                // for(let i=0; i<allListData.length; i++){
                //     if(allListData[i].key === _this.key){
                //         console.log('allListData[i].key ==>',allListData[i].key)
                //         this.a = 1
                //         console.log('aaaaaa11111',this.a)
                //     } else {
                //         this.a= 2
                //         console.log('aaaaaa222222',this.a)
                //     }
                // }
                
                // if(this.a === 2 ){
                    //加载提示
                    _this.$toast.loading({
                        duration: 0,
                        message: "加载中..."
                    });

                    this.axios({
                        //请求类型
                        method:'GET',
                        //请求地址
                        url:'http://api01.6bqb.com/toutiao/search?apikey=9152B061C54746870D248AA78B33EA05&tag=' +  _this.key
                        
                    }).then(result =>{


                        _this.allList = result
                        _this.allList.key = _this.key
                        // console.log('_this.allList ==>',_this.allList)

                        _this.allListData.push(_this.allList)
                        // console.log('_this.allListData',_this.allListData)
                        localStorage.setItem('allListData',JSON.stringify(_this.allListData))

                        _this.alllistData = result.data
                         _this.listData = _this.alllistData.data
                        //  _this.listData = _this.alllistData.data.slice(0,_this.count)
                        
                        //取消加载提示
                        this.$toast.clear();
                
                    }).catch(err => {
                        //取消加载提示
                        this.$toast.clear();
                    })
                //     return

                // } else {
                //     console.log(1324234234234)

                //     // _this.listData = allListData[i].data
                //     // console.log('_this.listData77777777',_this.listData)

                // }               
            },

            //点击跳转到详情页以及传参
            newsDetails(id,item){

                this.$router.push({
                    name: 'newsDetails',
                    params: {
                        id: id,
                        item:item
                    }
                })
                //获取历史数据
                let historyData = localStorage.getItem('historyData')
                //防止空用户
                historyData = historyData ? JSON.parse(historyData) : [];

                // 增加到数组中
                historyData.unshift(item)
                
                // 保存在本地
                localStorage.setItem('historyData',JSON.stringify(historyData))
            }
        }
     
    }
</script>

<style lang="less" scoped>

    .search-box{
        width:calc(100% - .6rem);
        height: 1.5rem;
        background: #e498bc;
        margin-top: 24px;
        padding:0 .3rem;

        .search{
            width:calc(100% - 1.2rem);
            margin-top: .15rem;
        }

        .van-search {
            padding: 0;
            height: 1.2rem;
            border-radius: .2rem;          
        }
        .van-search .van-cell {
                padding: 0;
                background: #fff;
        }
        .van-icon{
            height: .3rem;
        }
        .relase{
            width: .8rem;
            height: 1.2rem;
            margin-top: .15rem;
            color: #fff;
            font-size: .3rem;
            text-align: center;

        
        }
    }
    .tabs-list-box{

        .content-box{
            width: 100%;
            height:13.1rem;
            background: #f5f5f5;
            overflow-y: auto;
        }
    }

    .content{
        margin-bottom: .3rem;

        .header{
            width:calc(100% - 1rem);
            height: .8rem;
            background: #fff;
            line-height: .8rem;
            padding: 0 .5rem;
            >span:nth-child(1){
                margin-left: .3rem;
            }
            >span:nth-child(2){
                margin-right: .3rem;
            }
        }
        .news-content{
            width:calc(100% - 1rem);
            height: 3rem;
            background: #fff;
            line-height: .8rem;
            padding: 0 .5rem;

            .img{
                width: 3rem;
                height: 2.1rem;
                background: #ddd;
                margin-top: .45rem;
            }
            .text{
                width:calc(100% - 3.3rem);
                height: 100%;
                // background: #e498bc;

                .title{
                    font-size: .4rem;
                    font-weight: bold;
                    margin-top: .1rem;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;
                    
                }
                .abstract{
                    height: 1.47rem;
                    color: #595959;
                    line-height: .5rem;
                    font-size: .35rem;
                    margin-top: .3rem;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 3;
                    overflow: hidden;
                }
            }
        }


    }

    


</style>